import { useEffect, useState } from "react";
import categorias from "../Services/categorias.json";
import { database } from "../Services/firebase";


const initialValues = {
    title: "",
    imageUrl: "",
    stock: 0,
    custo: 0,
    categoria: -1,
    tipo: "",
    taxaImport: false,
    freteGratis: false,
    custoFrete: 0,    
    margemLucro: 0,
  };

export default function Form(props) {

    let [values, setValues] = useState(initialValues);
    const [freteGratis, setFreteGratis] = useState(false);


    // useEffect( () => {
    //     if(props.idAtual === '') {
    //         setValues({
    //             ...initialValues
    //         })
    //     } else {
    //         setValues({
    //             ...props.dadosAnuncios[props.idAtual]
    //         })
    //     }
    // }, [props.idAtual, props.dadosAnuncios]);

    useEffect(() => {
        if(props.idAtual) {
            database.get(`anuncio/${props.idAtual}`)
            .then((response) => {
                console.log(response.data);
                setValues(response.data);
            })
        }
    }, [])
      
        

   
    function onChange(ev) {
        const{ name, value } = ev.target;
        setValues({ ...values, [name]: value });
    };

    function toggle() {
        setFreteGratis(!freteGratis)
        
    };

    function onSubmit(ev) {
        ev.preventDefault();
        props.addEedit(values)
    };
    

    return(
        <form autoComplete="off" onSubmit={onSubmit}>            
            <div className="row mb-3">
                <input type="text" name="title" value={values.title} placeholder="TÃ­tulo" className="form-control form-control-sm" onChange={onChange} />
            </div>
            <div className="row mb-3">
                <input type="url" name="imageUrl" value={values.imageUrl} placeholder="Imagem URL" className="form-control form-control-sm" onChange={onChange} />
            </div>
            <div className="row mb-3">
                <input type="number" name="stock" value={values.stock} placeholder="Estoque" className="form-control form-control-sm" onChange={onChange} />
            </div>           
            <div className="row mb-3">
                <input type="number" name="custo" value={values.custo} placeholder="Custo" className="form-control form-control-sm" onChange={onChange} />
            </div>
            <div className="row mb-3">
                <select name="categoria" value={values.categoria} className="form-select form-select-sm" onChange={onChange}>
                    <option value={-1}>Categoria</option>
                    {
                        categorias.map((item, i) => (
                            <option key={"categorias" + i} value={i}>{item.nome}</option>
                        ))
                    }
                </select>
            </div>
            <div className="row mb-3">
                <div className="col-auto">
                    <div className="form-check">
                        <input type="radio" name="tipo" className="form-check-input" value="ClÃ¡ssico" onChange={onChange} />
                        <label className="form-check-label">ClÃ¡ssico</label>
                    </div>
                </div>
                <div className="col-auto">
                    <div className="form-check">
                        <input type="radio" name="tipo" className="form-check-input" value="Premium" onChange={onChange} />
                        <label className="form-check-label">Premium</label>
                    </div>
                    </div>
            
            </div>
            <div className="row mb-3">
                    <div className="form-check form-switch">                           
                        <input type="checkbox" name="freteGratis" role="switch" className="form-check-input" onClick={toggle} />
                        <label className="form-label">Frete GrÃ¡tis</label>            
                    </div>
                    {
                        freteGratis && (
                            <>
                                <input type="number" name="custoFrete" placeholder="R$" className="form-control form-control-sm" onChange={onChange} /> 
                            </>
                        )
                    }  
            </div>
            <div className="row mb-2">
                <div className="form-check form-switch">                           
                    <input type="checkbox" name="taxaImport" className="form-check-input" onChange={onChange} />
                    <label className="form-label">Taxa de ImportaÃ§Ã£o</label>  
                </div>            
            </div>
            <div className="row mb-3">
                <input type="number" name="margemLucro" placeholder="Margem de Lucro" className="form-control form-control-sm" onChange={onChange} />
            </div>
            <div className="row">
               <input type="submit" value={ props.idAtual === '' ? 'Salvar' : 'Atualizar'} className="btn btn-primary" />
            </div>
        </form>    
        );
};